import React, { Component } from 'react';
import Clipboard from '../../src';
import ZhCn from "bee-locale/build/zh_CN.js";
import ZhTw from "bee-locale/build/zh_TW.js";
import EnUS from "bee-locale/build/en_US.js";

class Demo4 extends Component {
    render() {
        function success() {
            console.log('success');
        }

        function error() {
            console.log('error');
        }

        return (
            <Clipboard locale={ZhTw.Clipboard}  action="copy" text="-" success={success} error={error}>

            </Clipboard>
        )
    }
}

export default Demo4;


