import React, { Component } from 'react';
import Clipboard from '../../src';

class Demo1 extends Component {
    render() {
        function success() {
            console.log('success');
        }

        function error() {
            console.log('error');
        }

        return (
            <Clipboard action="copy" text="-" success={success} error={error}>

            </Clipboard>
        )
    }
}

export default Demo1;


