import React, { Component } from 'react';
import Clipboard from '../../src';

class Demo2 extends Component {
    render() {
        function success(){
           console.log('success');
        }
        function error(){
           console.log('error');
        }
        return (
            <div>
                <div id="copyContent" >-</div>
                <Clipboard action="copy"  target='#copyContent' success={success} error={error}>
                </Clipboard>
            </div>
        )
    }
}

export default Demo2;


